@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(100, 30, 226);
  background: linear-gradient(
    112deg,
    rgba(100, 30, 226, 1) 0%,
    rgba(23, 3, 122, 1) 100%
  );
  font-family: 'Roboto', sans-serif;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar.navbar-expand-lg {
  background-color: #fff !important;
}
form {
  display: flex;
  margin: 1rem 0;
}
h1 {
  font-size: 1rem;
}
.center-element {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 30rem;
  margin: 10vh auto;
  background: rgb(6, 26, 3);
  color: #fff;
  border-radius: 9px;
  height: 100%;
  min-height: 27rem;
}
.center-element > h1 {
  font-size: 1.5rem;
}

.addItem {
  display: inline-block;
  width: 76%;
}
.addItem input {
  border: none;
  border-radius: 4px;
}
.addItem input:focus {
  /* outline: none; */
  box-shadow: none;
  /* outline-offset: 0; */
  border-color: black;
}
.button {
  padding: 7px;
  border: none;
  border-radius: 4px;
  background: rgb(30, 132, 226);
  background: linear-gradient(
    112deg,
    rgba(30, 132, 226, 1) 0%,
    rgba(102, 7, 155, 1) 100%
  );
  color: #fff;
  margin-left: 4px;
}
.operation {
  margin-top: 5px;
}
.fa-trash-alt {
  align-self: center;
  margin-right: 8px;
}
.deleteAll {
  display: block;
  border-radius: 6px;
  background-color: red;
  border: none;
  padding: 5px 8px;
  margin-bottom: 1rem;
  color: #fff;
  font-weight: 500;
  margin-top: 0.5rem;
}

.todoItem {
  display: flex;
  justify-content: space-between;
  align-content: center;
  text-transform: capitalize;
  background: rgb(30, 132, 226);
  background: linear-gradient(
    112deg,
    rgba(30, 132, 226, 1) 0%,
    rgba(12, 7, 155, 1) 100%
  );
  margin: 10px 0;
  border-radius: 5px;
  padding: 0 10px;
  cursor: pointer;
  position: relative;
  z-index: 1000;
}
.line {
  opacity: 0;
  position: absolute;
  width: 108%;
  height: 1.5px;
  background-color: greenyellow;
  top: 50%;
  left: -18px;
  transition: .3s ease;
}
.todoItem.active {
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}
.todoItem.active .line{
  opacity: 1;
}
.title.active {
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}
.title.active .line{
  opacity: 1;
}


